<template>
  <div>
    작성자:{{ name }}
    제목:{{ review?.title }}
    별점:<b-form-rating v-model="value" style="border:0px solid black; background-color:white;" variant="warning" class="mb-2" disabled></b-form-rating>
    내용:{{ review?.detail }}
    작성일시:{{ review?.created_date }}
    <div>
      <!-- 로그인 유저만 보이게 v-if 추가예정 -->
    <b-button variant="primary" v-on:click="putReview">
      수정
    </b-button>
    <b-button variant="Danger" v-on:click="deleteReview">
      X
    </b-button>
    </div>
  </div>
</template>

<script>
import axios from '@/service/axios'

const SERVER_URL = 'https://i8a102.p.ssafy.io/api'

export default {
    name:"ReviewListItem",
    props:['review'],
    data(){
      return{
        name:"",
        value:this.review.star_point,
      }
    },
    methods:{
      putReview(){
        this.$router.push({name:"Review", query:{ rid:this.review.product_comment_seq }})
      },
      deleteReview(){
        axios({
          method:"delete",
          url:``
        })
        .then((res)=>{
          console.log(res)
          this.$router.push({ name:'ProductDetail', params:{ pid:`${this.$route.params.pid}` }})  // 나중에 검토하기 
        })
        .catch((err)=>{
          console.log(err)
        })
      },
      getUserName(){
            axios({
                method:"get",
                url:`${SERVER_URL}/user/all/${this.review.user_seq}`
            })
            .then((res)=>{
                this.name = res.data.user_id
            })
        }

    },
    created(){
        this.getUserName()
    },

}
</script>

